@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/gilroy-regular.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/gilroy-medium.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 600;
  src: url(../fonts/gilroy-semibold.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../fonts/gilroy-bold.ttf);
}
@font-face {
  font-family: "gilroy";
  font-style: normal;
  font-weight: 800;
  src: url(../fonts/Gilroy-extrabold.otf);
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  font-family: "gilroy", "Helvetica", sans-serif;
  font-style: normal;
}

a {
  text-decoration: none;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}

main {
  flex: 1 1 auto;
}

.header {
  width: 100%;
}
.header__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.logo__img {
  padding-top: 33px;
}

.menu__list {
  display: flex;
  list-style: none;
  padding-top: 39px;
}
.menu__list-items {
  margin-left: 72px;
  color: #2C2F36;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.menu__buttons {
  margin-top: 24px;
}

.menu__list-button {
  min-width: 256px;
  min-height: 48px;
  background-color: #FB8C00;
  border-radius: 88px;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  border: none;
  position: relative;
  padding-left: 14px;
}

.menu__list-button::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 14px;
  background-image: url(../../img/main/calculate-button.svg);
  left: 25px;
  top: 15px;
}

.top-section {
  max-width: 1600px;
  margin: 0 auto;
  padding-top: 109px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.top-section__container {
  margin-left: 200px;
}

.top-section::before {
  position: absolute;
  content: "";
  width: 1600px;
  height: 940px;
  top: 0;
  background-image: url(../../img/main/background-color-header.jpg);
  background-repeat: no-repeat;
  object-fit: contain;
  background-position: center;
  z-index: -3;
}

@media (min-width: 375px) and (max-width: 765px) {
  .top-section {
    padding-top: 40px;
    max-width: 375px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
  }
  .top-section__container {
    margin-left: 0px;
    padding-top: 73px;
  }
}
.top-section-title {
  font-weight: 800;
  font-size: 72px;
  line-height: 88px;
  color: #2C2F36;
  width: 370px;
}
.top-section-title-color {
  color: #FB8C00;
}

@media (min-width: 375px) and (max-width: 765px) {
  .top-section-title {
    max-width: 319px;
    max-height: 152px;
    font-size: 62px;
    line-height: 76px;
  }
}
.paragraph-top {
  display: flex;
  justify-content: left;
}
.paragraph-top__text {
  margin-left: 63px;
  position: relative;
  margin-top: 199px;
}
.paragraph-top__text:nth-child(1) {
  margin-left: 0;
  max-width: 162px;
}
.paragraph-top__text:nth-child(2) {
  max-width: 195px;
}
.paragraph-top__text:nth-child(3) {
  max-width: 186px;
}

.paragraph-top__text::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-color: #fff;
  background-position: center;
  background-image: url(../../img/main/layer.svg);
  background-repeat: no-repeat;
  border-radius: 50px;
  top: -84px;
}

.paragraph-top__text:nth-child(1)::before {
  background-image: url(../../img/main/pencil.svg);
}

.paragraph-top__text:nth-child(3)::before {
  background-image: url(../../img/main/printer.svg);
}

.paragraph-top__text:nth-child(1)::after,
.paragraph-top__text:nth-child(2)::after {
  position: absolute;
  content: "";
  width: 3px;
  height: 150px;
  background-color: #9BA9B5;
  opacity: 0.2;
  border-radius: 36px;
  top: -95px;
  right: -30px;
}

@media (min-width: 375px) and (max-width: 765px) {
  .paragraph-top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .paragraph-top__text {
    margin-left: 0;
    position: relative;
  }
  .paragraph-top__text:nth-child(1) {
    max-width: 345px;
  }
  .paragraph-top__text:nth-child(2) {
    max-width: 345px;
  }
  .paragraph-top__text:nth-child(3) {
    max-width: 276px;
  }

  .paragraph-top__text:nth-child(1)::after,
.paragraph-top__text:nth-child(2)::after {
    position: absolute;
    content: "";
    width: 150px;
    height: 3px;
    background-color: #9BA9B5;
    opacity: 0.2;
    border-radius: 36px;
    top: 64px;
    left: 0px;
  }
}
.section-printer__image {
  position: relative;
  top: -40px;
}

@media (min-width: 375px) and (max-width: 765px) {
  .section-printer__image {
    max-width: 385px;
    max-height: 236px;
    object-fit: contain;
  }
}
.details {
  padding-top: 307px;
  max-width: 1200px;
  margin: 0 auto;
}
.details__elements {
  padding-top: 129px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  align-items: end;
  justify-items: center;
  text-align: center;
  gap: 126px 30px;
}

.details-title {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.element-items {
  min-height: 284px;
  min-width: 370px;
  background: radial-gradient(98.97% 276.64% at 1.03% 40.7%, #EBF6FF 0%, #EFF8FF 100%);
  border-radius: 35px;
}

.element-items__title {
  max-width: 215px;
  padding-left: 78px;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
}

.element-items__text {
  max-width: 228px;
  padding-left: 71px;
  padding-bottom: 40px;
}

.element-items:nth-child(7) > .element-items__title {
  max-width: 230px;
}

.element-items:nth-child(8) > .element-items__text {
  max-width: 262px;
  padding-left: 54px;
}

.element-items:nth-child(6) > .element-items__title {
  max-width: 259px;
  padding-left: 56px;
}

.element-items-img {
  height: 200px;
}

.element-items__image {
  transform: translateY(-40px);
}

.element-items:nth-child(7) > .element-items-img {
  height: 175px;
  transform: translateY(40px);
}

.element-items:nth-child(1) > .element-items-img {
  height: 175px;
}

.condition-work-container {
  padding-top: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}